.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    z-index: 1000;

    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-bottom: 1px solid var(--glass-border);
    transition: all 0.3s ease;
}

@media (prefers-color-scheme: dark) {
    .navbar {
        background: rgba(28, 27, 31, 0.8);
    }
}

.logo {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--md-sys-color-primary);
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 8px;
}

.navLinks {
    display: flex;
    gap: 24px;
    align-items: center;
}

.link {
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--md-sys-color-on-surface);
    text-decoration: none;
    transition: color 0.2s ease;
}

.link:hover {
    color: var(--md-sys-color-primary);
}

.mobileMenuBtn {
    display: none;
    background: none;
    border: none;
    color: var(--md-sys-color-on-surface);
    cursor: pointer;
    padding: 8px;
}

.mobileMenu {
    display: none;
}

@media (max-width: 768px) {
    .navLinks {
        display: none;
    }

    .mobileMenuBtn {
        display: block;
    }

    .mobileMenu {
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 64px;
        left: 0;
        right: 0;
        background: var(--md-sys-color-surface);
        border-bottom: 1px solid var(--glass-border);
        padding: 16px;
        gap: 16px;
        transform: translateY(-100%);
        opacity: 0;
        transition: all 0.3s ease;
        z-index: 999;
        pointer-events: none;
    }

    .mobileMenu.open {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
        display: flex;
    }
}