.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    box-sizing: border-box;
    min-width: 64px;
    border: none;
    outline: none;
    line-height: inherit;
    user-select: none;
    -webkit-appearance: none;
    overflow: hidden;
    vertical-align: middle;
    background: transparent;

    font-family: 'Inter', sans-serif;
    font-size: 0.875rem;
    font-weight: 500;
    letter-spacing: 0.1px;
    line-height: 20px;

    border-radius: 100px;
    /* Fully rounded */
    padding: 0 24px;
    height: 40px;

    transition: background-color 200ms cubic-bezier(0.2, 0, 0, 1),
        box-shadow 200ms cubic-bezier(0.2, 0, 0, 1),
        color 200ms cubic-bezier(0.2, 0, 0, 1);
    cursor: pointer;
}

/* Filled Button (Primary) */
.filled {
    background-color: var(--md-sys-color-primary);
    color: var(--md-sys-color-on-primary);
    box-shadow: 0px 1px 3px 1px rgba(0, 0, 0, 0.15), 0px 1px 2px 0px rgba(0, 0, 0, 0.3);
}

.filled:hover {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), white 8%);
    box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.filled:active {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), black 12%);
}

/* Outlined Button */
.outlined {
    border: 1px solid var(--md-sys-color-secondary);
    color: var(--md-sys-color-primary);
}

.outlined:hover {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), transparent 92%);
}

.outlined:active {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), transparent 88%);
}

/* Text Button */
.text {
    color: var(--md-sys-color-primary);
    padding: 0 12px;
}

.text:hover {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), transparent 92%);
}

.text:active {
    background-color: color-mix(in srgb, var(--md-sys-color-primary), transparent 88%);
}

/* Glass Variant */
.glass {
    background: var(--glass-background);
    backdrop-filter: blur(var(--glass-blur));
    -webkit-backdrop-filter: blur(var(--glass-blur));
    border: 1px solid var(--glass-border);
    color: var(--md-sys-color-on-surface);
    box-shadow: var(--glass-shadow);
}

.glass:hover {
    background: rgba(255, 255, 255, 0.35);
}